<template>
  <div>
    <div id="xigua"></div>
  </div>
</template>

<script>
import mp4 from '@/assets/img/trailer.mp4'
export default {
  name: 'nsVideo',
  props: {
    src: {
        type: String,
        default: ''
    },
    autoplay: { // / 
        type: Boolean,
        default: false
    },
    disableProgress: { // 
        type: Boolean,
        default: false
    },
  },
  data(){
    return {
        player: null,
        flag: '',
    }
  },
  created(){},
  mounted(){
    this.player = new window.Player({
      id: 'xigua',
      url: this.src ? this.src : mp4,
      autoplay: true,
      // disableProgress: true,
      allowSeekPlayed: true,
      // rotateFullscreen: true,
      // whitelist: ['']
    });
    this.player.once('ready', () => {
      // 
      this.flag = setInterval(() => {
        // 
        let preTime = JSON.parse(window.sessionStorage.getItem('videoPlayTime'));
        // 1
        console.log(Math.round(this.player.currentTime), preTime);
        if(Math.round(this.player.currentTime) - preTime > 1.1) this.player.currentTime = preTime;
        // 
        window.sessionStorage.setItem('videoPlayTime', JSON.stringify(Math.round(this.player.currentTime)))
      }, 100)
    })
    this.$emit('player', this.player);
    this.player.on('requestFullscreen',()=>{
      console.log('');
      this.player.pause()
    })
    this.player.on('exitFullscreen',()=>{
      this.player.play()
    })
  },
  beforeDestroy(){
    clearInterval(this.flag);
  },
  methods:{}
}
</script>

